body {
background-image:url("pexels-johannes-plenio-1118873.jpg");
background-repeat: no-repeat;
display: 100%;
opacity: 50;
background-size:100% ;


}
.app{
    
    text-align: center;
    font: 100px;
    
}

.title{
    
   border: solid grey;
   margin-left: 50px;
margin-right: 50px;
   font-size: 100px;
   background-color: aliceblue;
   font-style: italic;
}
.data{
    background-color: rgba(121, 121, 211, 0.452);
   margin-left: 500px;
   margin-right: 500px;
   height: 300px;
}
.city-name{
    font-family: Arial, Helvetica, sans-serif;

}
.weather-info{
    font-size: xx-large;
    line-height: 60px;
 
}
.input{
    width: 350px;
    height: 30px;


    border: solid rebeccapurple;
    border-radius: 10px 10px 10px 10px;
}
.button{
    margin-left: 10px;
border-radius: 12px 12px 12px 12px;
width: 88px;
height:40px
}
.button:hover{
border: solid black 1px;

}

